.about-wrapper {
    margin-top: 31.5px;
    position: relative;
    height: calc(100vh - 31.5px);
    max-height: calc(100vh - 31.5px);

    &::after {
        content: "";
        position: absolute;
        width: 90vw;
        border-bottom: 1px solid #bebebe;
        top: 35.3px;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .header__anchor {
        color: #bebebe;
    }
    .about__main {
        display: flex;
        height: 80%;
    }
    .about__info {
        width: 90%;
        display: flex;
        flex-direction: row;
        margin: 75px auto 0;

        .about__img-cont {
            flex: 1 1 50%;
            .about__img {
                display: block;
                object-fit: contain;
                object-position: center center;
                width: 100%;
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
    .about__text-content {
        flex: 1 1 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        color: #202020;
        h2 {
            font-size: 60px;
        }
        span {
            font-size: 24px;
        }
        .about__name {
            font-size: 36px;
        }
    }
    .about__bottom {
        margin-top: 75px;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        color: #cecece;
    }
}
#square {
    margin-top: 22.2px;
    width: 32px;
    height: 32px;
    background: #bebebe;
}
